.product{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden; 
}

.p_browser{
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.p_circle{
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
  color: aliceblue;
  }

  .p_img{
    width: 100%;
    height: 100%;
    transition: all 10s ease;
  }

  
.product:hover .p_img {
    transform: translateY(-100%);
  }

  @media screen and (max-width: 480px) {
  .product {
      width: 80%;
      height: 30vh;
      object-fit: contain;
    }
  }