.toggle{
    width:50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: white;
    position: fixed;
    top:10px;
    right: 10px;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.toggle_icon{
    width: 15px;
}

.toggle_btn{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}
